"use client";

import React, { useState } from 'react';
import QueueYouTubeLinks from '../components/QueueYouTubeLinks';
import Player from '../components/Player';

const HomePage: React.FC = () => {
  const [currentVideoId, setCurrentVideoId] = useState<string | null>(null);

  const handlePlayVideo = (videoId: string) => {
    setCurrentVideoId(videoId);
  };

  return (
    <div className="container mx-auto p-4">
      <h1 className="text-3xl font-bold mb-4">Karaoke Queue</h1>
      <QueueYouTubeLinks onPlayVideo={handlePlayVideo} />
      {currentVideoId && <Player videoId={currentVideoId} />}
    </div>
  );
};

export default HomePage;
